.header {
    width: 100%;
    $color-black: black;
    $color-white: white;
    $hover-food: #4fb68d;

    .welcome_header {
        color: $color-white;
        background: $color-black;
        height: 44px;
        display: flex;
        align-items: center;
        font-size: 14px;
        font-weight: 400;

        @media (max-width:991px) {
            height: 31px;
        }
    }

    .main_header {
        height: 110px;
        display: flex;
        justify-content: space-between;
        background: none;
        position: absolute;
        z-index: 9999;

        @media (max-width:991px) {
            position: relative;
        }

        .imgExpand {
            background-image: url('../../../assets/img/pngwing.com.png');
            width: 40px;
            height: 40px;
            background-size: contain;
            background-repeat: no-repeat;
        }

        .header_row {
            width: 100%;

            @media (max-width:991px) {
                height: 65px;
                justify-content: center;
            }

            .header_img {
                display: flex;
                align-items: center;
                justify-content: center;
            }

            img {
                height: fit-content;
            }

            .nav_header,
            .search span {
                @media (max-width:991px) {
                    display: none !important;
                }
            }

            //EXPAND
            .expandheader {
                z-index: 9999;
                position: absolute;
                left: 15px;
                top: 20px;
                display: none;

                .overlay {
                    position: fixed;
                    top: 0;
                    left: 0;
                    bottom: 0;
                    right: 0;
                    z-index: 999;
                    background: rgba(0, 0, 0, 0.521);
                }

                .navleft {
                    position: fixed;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    width: 350px;
                    background: $color-white;
                    z-index: 99999;
                }

                p:hover {
                    cursor: pointer;
                }

                @media (max-width:991px) {
                    display: block;
                }
            }

            .nav_header {
                ul {
                    display: flex;
                    list-style: none;
                    align-items: center;

                    a {
                        text-decoration: none;

                        .fr {
                            background: white;
                            animation: fadeIn ease 1s;

                            li:hover {
                                border: none;
                            }

                            display:none;
                            z-index: 9;
                            position: fixed;
                            top: 140px;
                            left: 0;
                            right: 0;
                            height: 374px;

                            .freshfood {
                                padding: 30px;
                                box-shadow: 0 0 3.76px 0.24px rgb(0 0 0 / 15%);

                                .row {
                                    margin: 0;
                                }

                                ul {
                                    display: flex;
                                    width: 100%;

                                    p {
                                        transition: 0.3s linear;
                                    }

                                    p:hover {
                                        color: $hover-food;
                                    }

                                    li {
                                        height: 100%;
                                    }

                                    ul {
                                        padding: 0;
                                        display: block;

                                        li {
                                            transition: 0.3s linear transform;
                                            font-weight: 400;
                                        }

                                        li:hover {
                                            color: $hover-food;
                                            transform: translateX(5px);
                                        }
                                    }
                                }

                                .foodImg {
                                    width: 100%;
                                    height: 130px;
                                    overflow: hidden;
                                }

                                .foodImg img {
                                    transition: linear transform 1s;
                                }

                                .foodImg img:hover {
                                    transform: scale(1.2);
                                }
                            }
                        }
                    }

                    .foodlink:hover .fr {
                        display: block;
                    }

                    .foodlink:hover>li {
                        border-bottom: 2px solid rgb(11, 9, 133);
                    }

                    .foodlink::after {
                        content: ' ';
                        display: inline-block;
                        color: $color-white;
                        height: 50px;
                        width: 90px;
                        position: absolute;
                    }

                    li:hover {
                        border-bottom: 2px solid rgb(11, 9, 133);
                    }

                    li {
                        font-weight: 700;
                        font-size: 14px;
                        margin-right: 10px;
                        padding: 5px;
                    }
                }
            }

            .navright {
                display: flex;
                justify-content: space-between;
                width: 80%;

                .nav_header {
                    display: flex;
                    align-items: center;

                    ul {
                        margin: 0;
                    }
                }

                @media (max-width:991px) {
                    position: absolute;
                    right: 15px;
                    top: 20px;
                    width: 25px;
                }
            }

            .search,
            .header_cart {
                display: flex;
                align-items: center;
                float: right;

                svg {
                    height: auto;
                    width: 21px;
                }
            }

            .imgCart {
                background-image: url('../../../assets/img/kisspng-computer-icons-shopping-bag-portable-network-graph-shopping-bag-svg-png-icon-free-download-374663-5c0e7688a347e3.2805000315444517206688.png');
                width: 24px;
                height: 26px;
                background-size: contain;
                background-repeat: no-repeat;
            }

            .search .imgSearch:hover {
                color: $hover-food;
                cursor: pointer;
            }

            .search {
                position: relative;
                font-size: 14px;

                .imgSearch {
                    background-image: url('../../../assets/img/search_img.png');
                    width: 30px;
                    height: 18px;
                    background-size: contain;
                    background-repeat: no-repeat;

                    @media (max-width:991px) {
                        display: none;
                    }
                }

                .searchForm {
                    animation: fadeIn ease 1s;
                    z-index: 99;
                    border: 2px solid $hover-food;
                    border-radius: 30px;
                    position: absolute;
                    top: 96px;
                    width: 1900%;
                    height: 46px;
                    right: -20px;
                    display: none;
                    align-items: center;
                    justify-content: space-between;
                    background: white;

                    @media (max-width:991px) {
                        width: 90vw;
                        margin: 0 2%;
                        top: 50px;
                        display: flex;

                        input {
                            width: 50% !important;
                        }
                    }

                    input {
                        outline: none;
                        border: none;
                        margin-left: 5px;
                        padding-left: 10px;
                        width: 50%;
                    }
                }

                .selectForm {
                    width: 30%;
                    margin-left: auto;
                    margin-right: 5px;
                    max-width: 150px;

                    select {
                        display: block;
                        margin-left: auto;
                        width: 100%;
                        border: none;
                    }
                }

                .searchFormBtn {
                    background: $hover-food;
                    height: 110%;
                    width: 65px;
                    border-radius: 0 30px 30px 0;
                    margin-right: -2px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: 0.3s linear;
                    background-image: url('../../../assets/img/pngfind.com-search-icon-png-545056.png');
                    background-size: 30%;
                    background-position: center;
                    background-repeat: no-repeat;
                    color: white;
                }

                .searchFormBtn:hover {
                    cursor: pointer;
                }
            }
        }
    }

    .searchForm {
        display: none;
    }
}