.navLeft{
    background: white;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 350px;
    z-index: 9999;
    animation: fadeIn ease 1s;
    font-size: 14px;
    font-weight: 600;
    .navMenu{
        background: #253237;
        color: white;
    }
    ol{
        list-style: none;
        padding: 0;
    }
    li{
        line-height: 41px;
        min-height:41px;
        border-top: 1px solid #eee;
        padding-left: 10px;
        div{
            height: 41px;
            display: flex;
            justify-content:space-between;
            .navLeft_suf{
                padding-right: 10px;
                font-size: 22px;
            }
        }
    }
}
