.slider {
    z-index: 1;

    img {
        width: 100%;
    }

    position: relative;

    .slider-content {
        position: absolute;
        left: 5%;
        top: 29.5%;

        .slider-title {
            font-style: italic;
            font-size: 12px;
        }

        .slider-main h2 {
            margin-bottom: 0;
            font-weight: 600 !important;
        }

        button {
            background: #4fb68d;
            border-radius: 15px;
            border: none;
            color: white;
            font-size: 10px;
            font-weight: 600;
            height: 25px;
            min-width: 60px;
            margin-top: 10px;
            transition: 0.6s linear;
        }

        button:hover {
            background: black;
        }
    }

    .slider-direct {
        min-width: 30px;
        position: absolute;
        bottom: 10px;
        left: 49%;
        display: flex;
        justify-content: space-between;

        .direct {
            background: white;
            width: 13px;
            height: 13px;
            border-radius: 50%;
        }

        .direct:hover {
            cursor: pointer;
        }
    }

    @media (max-width:768px) {
        position: relative;

        .slider-title {
            font-size: 10px;
        }

        .slider-main h2 {
            font-size: 22px;
            font-weight: 600 !important;
            
        }
    }
}

.line {
    display: block;
    top: 38px;
    left: 0;
    border-bottom: 4px solid brown;
    width: 0%;
    height: 10px;
    z-index: 99;
    animation: line 5s linear infinite;

    @media (max-width:991px) {
        margin-top: 10px;
    }
}

.active-slider {
    background: #4fb68d !important;
}

.animation-slider1{
    animation: fadeIn1 1s;
    button{
        animation: zoomIn 1s linear;
    }
    .slider-title{
        animation: lefttoRight 1s linear;
    }
    h2{
        animation: fadeIn 1s linear;
    }
}

.animation-slider2{
    animation: fadeIn2 1s;
    button{
        animation: fadeIn 1s linear;
    }
    .slider-title{
        animation: zoomIn 1s ease-in-out;
    }
    h2{
        animation: righttoLeft 1s linear;
    }
}

