.banner{
    min-height: 195px;
    display: flex;
    padding: 0 15px;
    justify-content: space-between;
    margin-top: 10px;
    div{

        overflow: hidden;
    }
    img{
        width:100%;
        height: 100%;
        transition: 2s linear;
    }
    img:hover{
        transform:scale(1.2);
    }
    .img1{
        flex:1;
    }
    .img2{
        flex:2;
        margin: 0 15px;
    }
    .img3{
        flex:1;
    }
    .img-banner{
        min-height: 80%;
        width: 90%;
    }
    @media (max-width:768px){
        display: block;
        .img2{
            margin:10px 0;
        }
        .img-banner{
            height: auto;
            width: 100%;
            background-size: 100% 100%;
        }
    }
}