*{
    font-family: 'Open Sans', sans-serif;
    ul{
        padding:0;
    }
    a{
        color: black!important;
    }
    .show{
        display:flex!important;
    }
    .showBlock{
        display: block!important;
    }
    .hidden{
        display:none;
    }
    .main{
        position: relative;
    }
    @keyframes fadeIn {
        0% {opacity:0.3;}
        100% {opacity:1;}
    }
    @keyframes fadeIn1 {
        0% {opacity:0.8;}
        100% {opacity:1;}
    }
    @keyframes fadeIn2 {
        0% {opacity:0.8;}
        100% {opacity:1;}
    }

    @keyframes line {
        from {
            left:0;
            width:0%;
        }
        to {
            right:0;
            width:100%;
        }
    }

    @keyframes zoomIn {
        from {
            transform: scale(0.1)
        }
        to {
            transform: scale(1.0)
        }
    }
    @keyframes lefttoRight {
        from {
            transform: translateX(-15px)
        }
        to {
            transform: translateX(0)
        }
    }
    @keyframes righttoLeft {
        from {
            transform: translateX(10px)
        }
        to { transform: translateX(0)}
    }
}
